<template>
    <div class="wxz-container">
        <header-bar></header-bar>
        <div class="container-fluid">
            <div class="container">
                <div class="wxz-content clearfix">
                    <div class="row">
                        <div class="col-md-12 col-xs-12 text-center">
                            <h1 class="text-rem3">
                                那些年的历程
                            </h1>
                        </div>
                    </div>
                    <div class="row">
                        <div class="col-xs-12 text-center">
                            <img src="../../../static/images/line.png" class="img-responsive img-auto">
                        </div>
                    </div>
                    <div class="row">
                        <div class="col-md-12 col-xs-12">
                            <ul class="list-unstyled wxz-course-menu">
                                <li v-for="(items,index) in courseData" v-bind:key="index" class="wxz-course-list">
                                    <span class="wxz-course-data">{{items.time}}</span>
                                    <h4>{{items.company}}</h4>
                                    <p>{{items.introduce}}</p>
                                    <ul class="list-unstyled">
                                        <li v-for="(item,index) in items.timeintroduce" v-html="item.info"></li>
                                    </ul>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <footer-bar></footer-bar>
    </div>
</template>
<script>
import {headerBar,footerBar} from '../../components/export.js';
import "../../amtdata/courseData.js";
export default{
    data(){
        return{
            courseData:[],
        }
    },
    components:{
        headerBar,
        footerBar
    },
    mounted(){
        this.getCourseData();
    },
    methods:{
        getCourseData:function(){
            this.$axios.get('/api/CourseData').then(res=>{
                let vm =this;
                vm.courseData =res.data.Data
            })
        }
    }

}
</script>
<style scoped>
.wxz-course-menu{position: relative; padding: 70px 0 70px 145px;}
.wxz-course-list{border-left:2px dotted #999; padding:20px 0 20px 20px;}
.wxz-course-list:before{content:''; display: block; width: 14px; height: 14px; background-color: #fff; border:2px solid #999; position: absolute; left:139px; margin-top:13px; border-radius: 100%;}
.wxz-course-data{display: inline-block; position:absolute; left:0; background-color: #0DBAE4; padding: 10px 15px; color:#fff;}
.wxz-course-list p{word-wrap: break-word;
white-space: normal;
word-break: break-all; width: 100%;}
@media (max-width: 768px){
    .wxz-course-menu{padding: 0;}
    .wxz-course-list{border-left:0;}
    .wxz-course-list:before{display: none;}
    .wxz-course-data{position: initial; margin-bottom: 20px;}
    .wxz-course-list{padding-left:0;}
}
</style>



